import React from 'react'
import GatheringsIndexTemplate from 'intl/GatheringsIndexTemplate'
import { graphql } from 'gatsby'

const lang = {
  'gatherings': '回家聚集',
  'upcoming_gatherings': '即将来临的聚集',
  'previous_gatherings': '过去的聚集'
}

const IndexPage = ({ data }) => (
  <GatheringsIndexTemplate intl={lang} gatherings={data.allMarkdownRemark.edges}/>
)

export default IndexPage

export const pageQuery = graphql`
  query {
    allMarkdownRemark(
      sort: { order: ASC, fields: [ frontmatter___date ] },
      filter: { frontmatter: { lang: { eq: "zh-Hans" }, type: { eq: "gathering" } } }
    ) {
      edges {
        node {
          id
          frontmatter {
            path
            date(formatString: "MMMM DD, YYYY")
            title
            addToIndex
          }
        }
      }
    }
  }
`
